</template>

<script>
	import chatInput from '../../components/im-chat/chatinput.vue';
	import messageShow from '../../components/im-chat/messageshow.vue';
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				style: {
					pageHeight: 0,
					contentViewHeight: 0,
					footViewHeight: 90,
					mitemHeight: 0,
				},
             
	            biaoqing_num:0,
                ziduan_modify_flag: true,


				hot_list_comment: [],
				fetchPageNum_comment: 1,
				luyouData: null,

				comment_message: "",
				card_id: "",
               loacal_avatarUrl_loc: "",
               taptablefuc:true,
			   persondata:{
				   comment_candianzhan:[],
				   comment_isdislike:[],
				   handle_isdislike: [],
			   },

			}
		},
		computed: {
			...mapState(['hasLogin', 'userInfo', 'phonenum', 'openid'])

		},
		components: {
			chatInput,
			messageShow
		},
		onLoad: function(opotion) {

            console.log("card_id:", opotion.card_id)
			this.loacal_avatarUrl_loc=this.loacal_avatarUrl
			this.card_id = opotion.card_id
			this.getData_hot_list_comment();
			this.get_hot_card_list_ziduan_comment();

		
			this.style.contentViewHeight = uni.getSystemInfoSync().windowHeight - 80*(uni.getSystemInfoSync().windowWidth/750); //像素
			// console.log(this.style.pageHeight, this.style.contentViewHeight)

		},
		onUnload: function() {},

		methods: {
			...mapMutations(['login', 'logout']),
		
		    biaoqing_select(biaoqing_num){
				this.biaoqing_num=biaoqing_num;
				console.log(this.biaoqing_num)
			},
		
		
			getInputMessage: function(message) { //获取子组件的输入数据
				// console.log(message);
				// this.addMessage('customer', message.content, false);
				// this. win_bottom=0;
				console.log(message)
				this.pushData_comment(message.content)
				setTimeout(() => this.getData_hot_list_comment(), 1000)
				// this.getData_hot_list_comment();
			},

			// 			send_comment: function() {
			// 				console.log(this.comment_message)
			// 				this.pushData_comment()
			// 				this.comment_message = ""
			// 				this.getData_hot_list_comment();
			// 			},
			get_hot_card_list_ziduan_comment() {
				var that = this;
				uni.getStorage({
					key: 'hot_card_list',
					success: function(res1) {
						that.luyouData = res1.data.data;
						for (let i = 0; i < that.luyouData.length; i++) {
							if (that.luyouData[i].ziduan_modify_flag == 0 && that.luyouData[i].card_id == that.card_id) {
								that.ziduan_modify_flag = false;

							}
						}
						if (that.card_id == 'main') {
							that.ziduan_modify_flag = false;
						}
					},
				});

			},
			
// 			scrolltoupper(){
// 				this.getData_hot_list_comment()
// 			},
			
			gotozipinglun(index){
				uni.navigateTo({
					url: "../sub_comment/sub_comment?comment_id=" +Number(this.hot_list_comment[JSON.stringify(index)].comment_id)
					+"&card_id="+this.card_id+"&huifu="+Number(this.hot_list_comment[JSON.stringify(index)].huifu),
				})
				
			},
			tap_table() {
				// this.select_biaoqing_flag = false;
				// this.select_view_flag = false;
				// this. win_bottom=0;
				this.taptablefuc=!this.taptablefuc;
				
			},
			
		   tap_dislike(index){
			   var that=this;
				this.hot_list_comment[JSON.stringify(index)].fandui = Number(this.hot_list_comment[JSON.stringify(index)].fandui) + 1
		    	this.hot_list_comment[JSON.stringify(index)].comment_isdislike = true;
				this.persondata['comment_isdislike'].push(this.hot_list_comment[JSON.stringify(index)].comment_id);
				uni.setStorage({
					key: 'persondata',
					data: that.persondata,
					success: function() {
						console.log('setdata_persondata_success');
					}
				});
				this.upload_to_server({
					'card_id': this.card_id,
					'form': 'comment_fandui',
					'dianzhan_num': this.hot_list_comment[JSON.stringify(index)].dianzhan,
					'comment_id': Number(this.hot_list_comment[JSON.stringify(index)].comment_id),
					'fandui': this.hot_list_comment[JSON.stringify(index)].fandui,
					'nickName': this.userInfo.nickName,
					'phonenum': this.phonenum,
					'openid': this.openid,
					'avatarUrl': this.userInfo.avatarUrl,

				})
			
				
				
		   },
			pushData_comment: function(message) {
				// console.log('updata:',message)
				uni.request({
					url: "https://kaikaiomg.applinzi.com/uni_app_upload.php",
					method: "POST",
					data: {

						'form': 'comment',
						'comment_message': message,
						'card_id': this.card_id,
						'nickName': this.userInfo.nickName,
						'phonenum': this.phonenum,
						'openid': this.openid,
						'avatarUrl': this.userInfo.avatarUrl,
						'comment_type':'comment',
						'biaoqing_num':this.biaoqing_num,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {

					},
					fail: () => {
						uni.showModal({
							content: "请求失败，请重试!",
							showCancel: false
						})
					}
				})
			},
			click_dianzhan(index) {
				var that=this;
				if (this.hot_list_comment[JSON.stringify(index)].can_dianzhan!=false) { 
					console.log(JSON.stringify(index));
					this.hot_list_comment[JSON.stringify(index)].dianzhan = Number(this.hot_list_comment[JSON.stringify(index)].dianzhan) + 1;
					this.hot_list_comment[JSON.stringify(index)].can_dianzhan=false;
					// this.pushData_comment_dianzhan(this.hot_list_comment[JSON.stringify(e)])
					this.persondata['comment_candianzhan'].push(this.hot_list_comment[JSON.stringify(index)].comment_id);
					uni.setStorage({
						key: 'persondata',
						data: that.persondata,
						success: function() {
							console.log('setdata_persondata_success');
						}
					});
                    this.upload_to_server({
						'card_id': this.card_id,
						'form': 'comment_dianzhan',
						'dianzhan_num': this.hot_list_comment[JSON.stringify(index)].dianzhan,
						'comment_id': Number(this.hot_list_comment[JSON.stringify(index)].comment_id),
						'fandui': this.hot_list_comment[JSON.stringify(index)].fandui,
						'nickName': this.userInfo.nickName,
						'phonenum': this.phonenum,
						'openid': this.openid,
						'avatarUrl': this.userInfo.avatarUrl,
					})
				}

				else{
					uni.showToast({
						title: "谢谢!已经投票过啦" ,
						icon:"none"
					})
				}
			},

			getData_hot_list_comment: function() {
				var that=this;
				uni.request({
					url: 'https://kaikaiomg.applinzi.com/uni_app_download.php',
					method: "POST",
					data: {
						'form': 'hot_card_list_comment',
						'card_id': that.card_id,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (ret) => {
						if (ret.statusCode !== 200) {
							console.log("请求失败:", ret)
						} else {
							console.log("hot_card_list_comment:", ret.data)
							that.hot_list_comment = ret.data.data;
							uni.getStorage({
								key: 'persondata',
								success: function(res) {
									console.log("find_persondata", res.data);
									that.persondata=res.data;
									for (let i = 0; i < that.hot_list_comment.length; i++) {
										if (that.persondata.comment_candianzhan.indexOf(that.hot_list_comment[i].comment_id) != -1) {
											that.hot_list_comment[i].can_dianzhan= false
										}
										if (that.persondata.comment_isdislike.indexOf(that.hot_list_comment[i].comment_id) != -1) {
											that.hot_list_comment[i].comment_isdislike= true
										}
									}
									
								},
